@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700;900&display=swap);
html {
  position: relative;
  min-height: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  margin-top: 2em;
  /* bottom = footer height */
  padding: 25px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.TweetModal {
  width: 60%;
  padding: 50px;
}

.overlay {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
}

.modal {
  position: fixed;
  border-radius: 15px;
  z-index: 20;
  background: #fff;
  width: 400px;
  min-height: 175px;

  /* Center the modal */
  top: 30%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.modal__header {
  background: white;
  padding: 10px 40px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid lightgray;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.modal__main {
  padding: 20px;
}

.close-button {
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 36px;
}

.close-button:hover {
  color: lightblue;
}

#submitBtn {
  height: 2.5rem;
  width: 30%;
  border: none;
  background-color: lightblue;
  transition: all 0.1s linear;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  color: white;
  font-weight: 700;
  font-size: 1em;
  transition: all 0.1s linear;
  float: right;
    margin-bottom: 2em;
}



.tweetBtnSubmit, .tweetSideButton{
  height: 2.5rem;
  width: 60%;
  border: none;
  background-color: lightblue;
  transition: all 0.1s linear;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  color: white;
  font-weight: 700;
  font-size: 1em;
  transition: all 0.1s linear;
}

#submitBtn:hover, .tweetSideButton:hover {
  background-color: rgb(210, 226, 232);
  cursor: pointer;
}

.tweetSideButton{
  width: 80%;
}



.button:hover {
  background-color: rgb(210, 226, 232);
  border-radius: 100px;
  box-sizing: border-box;
  cursor: pointer;
}

input {
  color: grey;
  border: none;
  font-size: 19px;
  margin-top: 0.25em;
}

input:focus {
  outline: 0;
}


.addPhotoBtn{
 margin-top: 1.5em;
}

.preview-image{
  max-width: 100px;
}

.previewDiv{
  margin-left: 1.5em;
  margin-bottom: 1em;
  width: 100px;
  height: 100px;
  border: 2px solid lightgray;
  overflow: hidden;
}

.post_photo{
  height: 280px;
  width: 520px;
  object-fit: cover;
  border-radius: 15px;
}

.tweetDiv{
  display: flex;
  flex-direction: column;
}

#tweetInput {
  padding: 0.3em;
  margin-left: 0.25em;
  width: 80%;
  border-bottom: lightgray 1px solid;
}

.addPhoto{
  color: black;
  margin-left: 18%;
  margin-top: 0.5em;
  transition: all 0.1s linear;
}

.addPhoto:hover{
  color: lightskyblue;
  cursor: pointer;
}

.inputPhoto {
  display: flex;
  justify-content: space-between;
  justify-content: center;
}


.birdLogo {
  max-width: 30px;
  padding: 0.75em;
  transition: all 0.1s linear;
  float: left;
}

.birdLogo:hover {
  background-color: lightblue;
  border-radius: 100px;
  box-sizing: border box;
}

.hashtag {
  max-width: 18px;
  justify-self: center;
  align-self: center;
}

.menuItem {
  font-size: 1.5em;
  float: left;
  margin-left: 0.8rem;
  color: black;
}

.explore_wrapper {
  max-width: 60%;
  display: flex;
  flex-direction: column;
  margin: 1.5em auto;
}

.logInBtn{
  margin-top: 1rem;
  height: 2.5rem; 
  border: none;
  background-color: lightblue;
  transition: all .1s linear;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  border-radius: 100px;
  color: white;
  }
  
  .SignUpLink {
    text-decoration: none;
  }
  
  .logInBtn p {
    margin: 0 auto;
    align-self: center;
  }
  
  .logInBtn:hover {
    background-color: rgb(210, 226, 232);
    border-radius: 100px;
    box-sizing: border box;
    cursor: pointer;
  }
.logInBtn{
  margin-top: 1rem;
  height: 2.5rem; 
  border: none;
  background-color: lightblue;
  transition: all .1s linear;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  border-radius: 100px;
  color: white;
  }
  
  .SignUpLink {
    text-decoration: none;
  }
  
  .logInBtn p {
    margin: 0 auto;
    color: white;
    align-self: center;
  }
  
  .logInBtn:hover {
    background-color: rgb(210, 226, 232);
    border-radius: 100px;
    box-sizing: border box;
  }
.SignUpBarWrapper {
  border: 1px solid #e6ecf0;
  border-radius: 15px;
  padding: 0 0.75rem 0.75rem;
  margin: 0 1rem;
  width: 80%;
  display: flex;
  flex-direction: column;
}

.welcome_title{
  text-align: center;
}

.SignUpBarWrapper h1 {
  font-size: 24px;
  font-weight: 700;
  justify-self: flex-start;
  align-self: flex-start;
}

.SignUpBarWrapper p {
  font-weight: 700;
  text-align: left;
}

.tweet_wrapper {
  color: black;
  padding-top: 0.9em;
  padding-left: 0.9em;
  display: flex;
  border-top: lightgray solid 1px;
  border-left: lightgray solid 1px;
  border-right: lightgray solid 1px;
  transition: all 0.1s linear;
}

.tweet_wrapper:hover {
  background-color: rgb(233, 233, 233);
}

.profile_photo {
  background-color: white;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  margin-left: 0.25em;
}

.profile_photo img {
  max-width: 48px;
  max-height: 48px;
  border-radius: 50%;
}

.tweet_content {
  display: flex;
  margin-left: 0.25em;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  padding-left: 0.75em;
}

.username {
  width: 97%;
  display: flex;
}

.threeDots {
  color: grey;
}

.usernameText {
  margin-left: 0.25em;
  margin-right: 0.25em;

  color: lightslategray;
}

.profileLinks {
  text-decoration: none;
  color: black;
}

.profileLinks:hover {
  text-decoration: underline;
  color: black;
}

.dateText {
  margin: 0 0.25em;
  color: black;
  justify-self: flex-start;
}

.tweet_content div {
  margin-bottom: 0.25em;
}

.bodyText {
  color: rgb(45, 45, 45);
  margin-bottom: 1em;
}

.tweet_functions {
  display: flex;
  margin: 0.25em 0;
  width: 95%;
  justify-content: space-between;
}

.tweet_functions svg {
  height: 20px;
  border-radius: 50%;
  padding: 0.3em;
  color: grey;
  transition: all 0.1s linear;
}

.tweet_functions .chatBubble:hover {
  color: rgb(100, 159, 196);
  background-color: lightskyblue;
}

.tweet_functions .retweet:hover {
  color: rgb(109, 84, 145);
  background-color: rgb(179, 163, 201);
}

.tweet_functions .heart:hover {
  color: rgb(155, 19, 19);
  background-color: rgb(249, 0, 0);
}

.tweet_functions .link:hover {
  color: rgb(100, 159, 196);
  background-color: lightskyblue;
}

.tweet_photo{
  margin-top: .4em;
  max-height: 280px;
  width: 500px;
  object-fit: cover;
  border-radius: 15px;
}

.tweets_feed:last-child {
  border-bottom: solid 1px lightgray;
}

.main_grid {
  width: 95vw;
  display: grid;
  grid-template-columns: 1fr 600px 1fr;
  margin-bottom: 100px;
}

.main_content {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  color: white;
  /* margin-top: 1em; */
}

.sidebar {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}

.signupbar {
  grid-column: 3 / 4;
  grid-row: 1 / 2;
}

.project_text {
  font-size: 0.95em;
  color: darkgray;
  font-weight: 700;
  margin: 2rem;
}

.name_text {
  font-size: 0.95em;
  color: rgb(130, 118, 208);
  font-weight: 700;
}

.name_text {
  text-decoration: none;
}
.name_text:hover {
  color: black;
}

.uploadBtn, .firstUpload{
  text-align: center;
}
